<template>
  <div style="margin:0;padding:0;background:#ECEFF4;">
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320" style="position:absolute;top:-10px;">
  <path fill="#0099ff" fill-opacity="1" d="M0,96L30,112C60,128,120,160,180,197.3C240,235,300,277,360,256C420,235,480,149,540,133.3C600,117,660,171,720,160C780,149,840,75,900,74.7C960,75,1020,149,1080,197.3C1140,245,1200,267,1260,256C1320,245,1380,203,1410,181.3L1440,160L1440,0L1410,0C1380,0,1320,0,1260,0C1200,0,1140,0,1080,0C1020,0,960,0,900,0C840,0,780,0,720,0C660,0,600,0,540,0C480,0,420,0,360,0C300,0,240,0,180,0C120,0,60,0,30,0L0,0Z"></path>
</svg>
    <br>   
<br>

    <div class="row justify-content-center" style="height:100vh;">
      <div class="col-md-6 ">
        <br>
        <br>
        <br>
        <br>
        <div class="card animate__animated animate__backInDown">
          <div class="card-header text-center">
            <img
              src="@/static/icon.png"
              class="kinoLightBox img-fluid float-right"
              style="height:40px;"
            />
            <p class="text-center ml-5 text-2xl font-bold font-times ">
              LOGIN
            </p>
          </div>
          <div class="card-body shadow-lg rounded-lg">
            <div v-if="error" class="alert alert-danger">{{error}}</div>
            <form action="#" @submit.prevent="loginAuth" class="font-times">
              <div class="form-group row">
                <label for="email" class="col-md-4 col-form-label text-md-right">Username</label>

                <div class="col-md-6">
                  <input
                    id="email"
                    type="text"
                    class="form-control"
                    name="email"
                    value
                    required
                    autofocus
                    v-model="vdata.email"
                  />
                </div>
              </div>

              <div class="form-group row">
                <label for="password" class="col-md-4 col-form-label text-md-right">Password</label>

                <div class="col-md-6">
                  <input
                    id="password"
                    type="password"
                    class="form-control"
                    name="password"
                    required
                    v-model="vdata.password"
                  />
                </div>
              </div>

              <div class="form-group row mb-0">
                <div class="col-md-12 text-center">
                  <button
                    type="submit"
                    class="btn btn-primary font-times"
                    style="background:#000B76;"
                  >
                    <span class="typcn typcn-key"></span> Login
                  </button>
                </div>
              </div>
              <br />
              <p class="text-center font-times">Login administrasi</p>
              <br />
              <!-- <div v-if="cordova">
                <div class="col-md-12" @click="cordovaGoogleAuth">
                  <a class="btn btn-lg btn-outline-secondary text-sm btn-block" href="#">
                    <img
                      class="float-left pt-1"
                      src="https://img.icons8.com/color/16/000000/google-logo.png"
                    />Login Dengan Google
                  </a>
                </div>
              </div>
              <div v-else>
                <div class="col-md-12" @click="googleAuth">
                  <a class="btn btn-lg btn-outline-secondary text-sm btn-block" href="#">
                    <img
                      class="float-left pt-1"
                      src="https://img.icons8.com/color/16/000000/google-logo.png"
                    />Login Dengan Google
                  </a>
                </div>
              </div> -->
            </form>
            <!-- <button type="button" @click="facebookAuth" class="btn btn-sm btn-outline-dark text-green btn-block  "><span class="typcn typcn-social-facebook-circular"></span> Facebook</button> -->
            <br />
            <div class="text-center">
              <!-- <button type="button" @click="$router.push('/')" class="btn btn-sm btn-danger  "><span class="typcn typcn-arrow-left"></span> Login user</button> -->
            </div>
         
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
let db = firebase.firestore();

export default {
  data() {
    return {
      vdata: {
        email: "",
        password: "",
      },
      Oauth: false,
      cordova: false,
      error: null,
    };
  },
  methods: {
    loginAuth() {
      firebase
        .auth()
        .signInWithEmailAndPassword(
          this.vdata.email + "@gmail.com",
          this.vdata.password
        )
        .then((data) => {
          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified,
          };
          this.$store.commit("changeUsers", user);
          localStorage.setItem("users", JSON.stringify(user));
          this.$router.push("/admin");
        })
        .catch((err) => {
          this.error = err.message;
        });
    },
    googleAuth() {
      this.Oauth = true;
      const provider = new firebase.auth.GoogleAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then((data) => {
          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified,
            emailnotif: data.user.email,
            changepassword:"true",
            unit:{
              nama_unit:"BELUM ADA"
            }

          };
          console.log('user',user)
          this.$store.commit("changeUsers", user);
          localStorage.setItem("users", JSON.stringify(user));
          console.log("user", user);
          db.collection("users")
            .doc(data.user.uid)
            .set(user, { merge: true })
            .then((res) => {
              this.$router.push("/");
            });
        })
        .catch((err) => {
          alert("Oops. " + err.message);
        });
    },
    facebookAuth() {
      this.Oauth = true;
      const provider = new firebase.auth.FacebookAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then((data) => {
          let user = {
            uid: data.user.uid,
            displayName: data.user.displayName,
            email: data.user.email,
            emailVerified: data.user.emailVerified,
          };
          this.$store.commit("changeUsers", user);
          localStorage.setItem("users", JSON.stringify(user));
          this.$router.push("/");
        })
        .catch((err) => {
          alert("Oops. " + err.message);
        });
    },
    cordovaGoogleAuth() {
      this.Oauth = true;
      let that = this;
      window.plugins.googleplus.login(
        {
          webClientId:
            "344034493973-h7mrkij6k43btmspsm2kardkqobnfjho.apps.googleusercontent.com",
          offline: true,
        },
        function (obj) {
          firebase
            .auth()
            .signInWithCredential(
              firebase.auth.GoogleAuthProvider.credential(obj.idToken)
            )
            .then((data) => {
              let user = {
                uid: data.user.uid,
                displayName: data.user.displayName,
                email: data.user.email,
                emailVerified: data.user.emailVerified,
              };
              that.$store.commit("changeUsers", user);
              localStorage.setItem("users", JSON.stringify(user));
              db.collection("users")
                .doc(data.user.uid)
                .set(user, { merge: true });
              that.$router.push("/");
            })
            .catch((err) => {
              alert("Oops. " + err.message);
            });
        },
        function (msg) {
          alert("error: " + msg);
        }
      );
    },
  },
  mounted() {
    if (this.Oauth) {
    } else {
      // this.$store.dispatch("ceklogin", { true: false, false: true });
      // setTimeout(() => {
      //   if(this.$store.state.users.auth=='admin'){
      //     this.$router.push('/admin')
      //   }else if(this.$store.state.users.auth=='user'){
      //     this.$router.push('/user')
      //   }
      // }, 1000);
    }
    let that = this;
    document.addEventListener("deviceready", function () {
      that.cordova = true;
    });
  },
};
</script>